/* Указываем box sizing */
*,
*::before,
*::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    color: $dark;
    font-size: 16px;
}

.body-overflow {
    overflow: hidden;
}

/* Убираем внутренние отступы */
ul[class],
ol[class] {
    padding: 0;
}

/* Убираем внешние отступы */
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
    margin: 0;
}

/* Выставляем основные настройки по-умолчанию для body */
body {
    min-height: 100vh;
    scroll-behavior: smooth;
    text-rendering: optimizeSpeed;
    line-height: 1.5;
    font-family: 'HelveticaNeueCyr', sans-serif;
    font-weight: 400;
}

/* Удаляем стандартную стилизацию для всех ul и il, у которых есть атрибут class*/
ul[class],
ol[class] {
    list-style: none;
}

/* Элементы a, у которых нет класса, сбрасываем до дефолтных стилей */
a:not([class]) {
    text-decoration-skip-ink: auto;
}

/* Упрощаем работу с изображениями */
img {
    max-width: 100%;
    display: block;
}

/* Указываем понятную периодичность в потоке данных у article*/
article>*+* {
    margin-top: 1em;
}

/* Наследуем шрифты для инпутов и кнопок */
input,
button,
textarea,
select {
    font: inherit;
}

/* Удаляем все анимации и переходы для людей, которые предпочитай их не использовать */
@media (prefers-reduced-motion: reduce) {
    * {
        animation-duration: 0.01ms !important;
        animation-iteration-count: 1 !important;
        transition-duration: 0.01ms !important;
        scroll-behavior: auto !important;
    }
}

.container {
    max-width: 1230px;
    width: 100%;
    padding: 0 15px;
    margin: 0 auto;

    @include response(1023) {
        padding: 0 10px;
    }
}

button {
    cursor: pointer;
    background: 0 0;
    border: 0;

    &:focus {
        outline: none;
    }
}

img,
svg {
    display: block;
    max-width: 100%;
    height: auto;
}

h1 {
    font-size: 36px;
    line-height: 1.2em;
}

h2 {
    font-size: 18px;
    line-height: 1.2em;

    @include response(1023) {
        font-size: 16px;
    }
}

.sticker {
    border-radius: 4px;
    background: $blue;
    padding: 5px;
    padding-bottom: 3px;
    margin-right: 20px;

    @include response(1023) {
        padding: 6px 5px;
        padding-bottom: 2px;
    }

    &__text {
        display: block;
        letter-spacing: 0.03em;
        text-transform: uppercase;
        font-size: 12px;
        font-weight: 700;
        color: white;
        text-align: center;
        line-height: 1em;

        @include response(1023) {
            font-size: 10px;
        }
    }

    &--white {
        background: $white;

        .sticker__text {
            color: $dark;
        }
    }
}

.anim {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: 15px;
    height: 11px;
    margin-left: 14px;
    transition: all .3s;

    &__arrow-line {
        position: absolute;
        top: 50%;
        left: 0;
        display: block;
        width: 10px;
        height: 1px;
        background-color: white;
        -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
        transform: translateY(-50%);
        -webkit-transform-origin: 0 50%;
        -ms-transform-origin: 0 50%;
        transform-origin: 0 50%;
        -webkit-transition: -webkit-transform .2s ease-out;
        transition: -webkit-transform .2s ease-out;
        -o-transition: transform .2s ease-out;
        transition: transform .2s ease-out;
        transition: transform .2s ease-out, height .2s ease-out, -webkit-transform .2s ease-out;
    }

    svg {
        position: absolute;
        top: 50%;
        right: 0;
        display: block;
        width: 11px;
        height: 8px;
        -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
        transform: translateY(-50%);
        -webkit-transition: -webkit-transform .2s ease-out;
        transition: -webkit-transform .2s ease-out;
        -o-transition: transform .2s ease-out;
        transition: transform .2s ease-out;
        transition: transform .2s ease-out, height .2s ease-out, -webkit-transform .2s ease-out;

        polygon {
            fill: white;
        }
    }

    &--down {
        margin-left: 0;

        .anim__arrow-line {
            height: 12px;
            width: 1.5px;
            transform: translateX(-50%);
            left: 50%;
            top: initial;
            bottom: 0;
        }

        svg {
            bottom: 0;
            right: initial;
            left: 50%;
            transform: translateX(-50%);
        }
    }
}

html,
body {

    .swiper-button-prev,
    .swiper-button-next {
        width: 50px;
        height: 50px;
        background: rgba(0, 0, 0, 0.5);
        border-radius: 4px;
        z-index: 10;
        margin: 0;
        transform: translateY(-50%);
        transition: background-color .3s ease-in-out;

        &::before {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            width: 20px;
            height: 10px;
            transform: translate(-50%, -50%);
            background-repeat: no-repeat;
        }

        &::after {
            display: none;
        }

        &:hover {
            background: $dark;
        }
    }

    .swiper-button-prev {
        left: 20px;
        right: initial;

        &::before {
            background-image: url('../images/slider-prev.svg');
        }
    }

    .swiper-button-next {
        right: 20px;
        left: initial;

        &::before {
            background-image: url('../images/slider-next.svg');
        }
    }
}

.tooltip {
    @include response(1023) {
        width: 16px;
        height: 16px;
    }
}