.footer {
    border-radius: 4px;
    background: $dark;

    &__content {
        padding-top: 109px;
        padding-bottom: 81px;

        @include response(1023) {
            padding-top: 30px;
            padding-bottom: 0;

            .container {
                padding: 0;
            }
        }
    }

    &__bottom {
        border-top: 1px solid #5c5c55;
        padding-top: 20px;
        padding-bottom: 15px;

        @include response(1023) {
            padding-bottom: 7px;
            padding-top: 15px;
        }
    }

    &__wrap {
        display: flex;
        align-items: flex-start;

        @include response(1023) {
            flex-direction: column;
        }
    }

    &__top {
        margin-bottom: 56px;

        @include response(1023) {
            margin-bottom: 30px;
            padding-left: 10px;
            padding-right: 10px;
        }
    }

    &__logo {
        display: block;
        max-width: 150px;
        width: 100%;

        @include response(1023) {
            max-width: 100px;
        }
    }

    &__col {
        margin-right: 118px;
        flex-shrink: 0;

        &:last-child {
            margin-right: 0;
        }

        &:nth-child(2) {
            margin-right: 193px;
        }

        @include response(1023) {
            margin: 0;
            width: 100%;
            padding-bottom: 30px;
            padding-top: 30px;
            padding-left: 10px;
            padding-right: 10px;
            border-bottom: 1px solid #999;

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                border-bottom: 0;
            }
        }
    }

    &__item {
        margin-bottom: 33px;

        &:last-child {
            margin-bottom: 0;
        }

        @include response(1023) {
            margin-bottom: 30px;
        }
    }

    &__title {
        font-size: 18px;
        color: $orange;
        font-weight: 700;
        margin-bottom: 18px;

        @include response(1023) {
            margin-bottom: 15px;
        }
    }

    &__text {
        color: white;
        font-size: 18px;
        display: flex;
        flex-direction: column;
        margin-bottom: 10px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__link {
        color: white;
        font-size: 18px;
        text-decoration: none;

        &:hover,
        &:focus {
            text-decoration: underline;
        }
    }

    &__nav {
        display: flex;
        flex-direction: column;
    }

    &__nav-link {
        text-decoration: underline;
        color: white;
        font-size: 18px;
        margin-bottom: 25px;
        line-height: 1em;

        &:hover,
        &:focus {
            text-decoration: none;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__bottom-wrap {
        display: flex;
        align-items: center;

        @include response(1023) {
            justify-content: space-between;
        }
    }

    &__link-bottom {
        display: block;
        margin-right: 299px;
        font-size: 14px;
        text-decoration: none;
        color: #999;
        &:last-child {
            margin-right: 0;
        }

        &:hover,
        &:focus {
            text-decoration: underline;
        }

        @include response(1023) {
            margin-right: 30px;
            font-size: 12px;
        }
    }
}