@import 'mixins';
@import 'variables';

.button {
    display: flex;
    justify-content: center;
    align-items: center;
    background: $orange;
    border-radius: 4px;
    height: 40px;
    line-height: 40px;
    padding: 0 10px;
    text-decoration: none;
    transition: background-color .3s ease-in-out;

    &__circle {
        width: 20px;
        height: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 10px;
        background: $white;
        border-radius: 50%;
        transition: transform .3s ease-in-out;
    }

    &__text {
        display: block;
        font-size: 14px;
        color: $white;
        line-height: 1.2em;
        flex-shrink: 0;
        margin-top: 3px;
    }

    &__icon {
        transform: scale(1);
        transition: transform .3s ease-in-out;
    }

    &:hover,
    &:focus {
        background: $accent;

        .button__icon {
            transform: scale(1.7);
        }
    }

    &--arrow {
        padding-left: 15px;
        padding-right: 16px;

        svg {
            margin-left: 10px;
        }

        .button__text {
            font-weight: 700;
            font-size: 16px;
        }
    }

    &--transparent {
        background: 0 0;
        border: 1px solid $white;
        text-align: left;
        justify-content: space-between;
        padding-left: 20px;
        padding-right: 20px;
        border-radius: 4px;

        .button__text {
            font-size: 16px;
            font-weight: 700;
        }

        .button__circle {
            background: $orange;
            margin-right: 0;
        }
    }

    &--large {
        height: 60px;
        line-height: 60px;
        padding-left: 15px;
        padding-right: 16px;

        .button__text {
            font-size: 18px;
            font-weight: 700;
        }

        .button__circle {
            width: 30px;
            height: 30px;

            svg {
                width: 14px;
                height: 14px;
            }
        }

        &:hover,
        &:focus {
            .button__circle {
                svg {
                    transform: scale(1.2);

                    path {
                        fill: #DF3014;
                    }
                }
            }
        }


        @include response(1023) {
            height: 40px;
            line-height: 40px;

            .button__text {
                font-size: 16px;
            }

            .button__circle {
                width: 20px;
                height: 20px;

                svg {
                    width: 6px;
                    height: 6px;
                }
            }
        }
    }
}