.similar-offers {
    padding: 100px 0;

    @include response(1023) {
        padding: 50px 0;
    }

    &__title {
        font-size: 36px;
        margin-bottom: 30px;

        @include response(1023) {
            font-size: 24px;
            margin-bottom: 20px;
        }
    }

    &__slider {

        .swiper-wrapper {
            width: auto;
            display: flex;
            margin: 0 -15px;

            @include response(1200) {
                flex-wrap: wrap;
                margin: -15px;
            }

            @include response(1023) {
                margin: 0;
                width: 100%;
                flex-wrap: nowrap;
            }
        }
    }

    &__container {
        @include response(1023) {
            padding-right: 0;
        }
    }

    .swiper-slide {
        padding: 0 15px;
        width: calc(100% / 3);
        display: flex;

        @include response(1200) {
            width: 50%;
            padding: 15px;

            &:last-child {
                width: 100%;
                justify-content: center;
            }
        }

        @include response(1023) {
            width: 340px !important;
            padding: 0;
        }
    }

    &__inner {
        border-radius: 4px;
        background: $white;
    }

    &__img-wrap {
        position: relative;
        padding-bottom: 56.58%;

        .sticker {
            position: absolute;
            z-index: 4;
            top: 10px;
            left: 10px;
        }
    }

    &__img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        -o-object-fit: cover;
        border-radius: 4px;
    }

    &__content {
        padding-top: 30px;
        padding-bottom: 16px;
        padding-left: 20px;
        padding-right: 20px;

        @include response(1023) {
            padding: 20px 10px;
        }
    }

    &__subtitle {
        font-weight: 400;
        font-size: 16px;
        margin-bottom: 28px;

        @include response(1023) {
            margin-bottom: 30px;
        }
    }

    &__info-wrap {
        display: flex;
        align-items: center;
        margin-bottom: 28px;
        justify-content: space-between;

        @include response(1023) {
            justify-content: flex-start;
        }
    }

    &__info-item {
        width: 100%;
        margin-right: 18px;
        padding-right: 17px;
        display: flex;
        flex-grow: 1;
        align-items: center;
        border-right: 1px solid #fcb27d;

        &:last-child {
            margin-right: 0;
            padding-right: 0;
            border-right: 0;
        }

        @include response(1023) {
            padding-right: 10px;
            margin-right: 10px;
        }
    }

    &__info-title {
        font-size: 16px;
        margin-right: 5px;
        white-space: nowrap;
    }

    &__info-value {
        font-size: 14px;
        color: #969696;
    }

    &__address {
        font-size: 14px;
    }

    &__bottom {
        padding-top: 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__more {
        font-weight: 700;
        color: $orange;
        display: flex;
        text-decoration: none;
        align-items: center;

        .anim__arrow-line {
            background: $orange;
        }

        svg {
            margin-left: 10px;

            path {
                fill: $orange;
            }
        }

        &:hover,
        &:focus {
            color: $accent;
            text-decoration: underline;

            .anim__arrow-line {
                transform: translateY(-50%) scaleX(2);
            }

            svg {
                transform: translateX(10px) translateY(-50%);

                path {
                    fill: $accent;
                }
            }
        }

        @include response(1023) {
            span {
                &:not(.anim) {
                    font-size: 14px;
                }
            }

            .anim {
                margin-left: 10px;
            }
        }
    }

    &__price {
        display: flex;
        align-items: center;
    }

    &__numb {
        font-weight: 900;
        margin-right: 10px;
        font-size: 24px;

        @include response(1023) {
            font-size: 18px;
            margin-right: 5px;
        }
    }

    &__currency {
        font-size: 14px;
        font-weight: 900;

        @include response(1023) {
            font-size: 12px;
        }
    }
}