.select {
    &__list {
        background: white;
        border: 1px solid rgba(0, 0, 0, 0.1);
    }

    &__link {
        display: block;
        padding: 8px 14px;
        color: $dark;
        font-size: 14px;
        font-weight: 400;
        text-decoration: none;

        &:hover,
        &:focus {
            background: $orange;
            color: white;
        }
    }

    &__item {
        text-align: left;

        &:nth-of-type(2n-1) {
            .select__link {
                background: $white;

                &:hover,
                &:focus {
                    background: $orange;
                    color: white;
                }
            }
        }

        &.active {
            .select__link {
                background: $orange;
                color: white;
            }
        }
    }
}