.nav {
    position: fixed;
    z-index: 20;
    left: 0;
    width: 100%;
    bottom: 0;
    min-height: calc(100vh - 55.66px);
    height: calc(100vh - 55.66px);
    background: #F3F3F3;
    color: $dark;
    padding: 15px 10px;
    padding-bottom: 51px;
    overflow: hidden;
    overflow-y: auto;
    left: -100%;
    transition: all .4s ease-in-out;

    &.show {
        left: 0;
    }

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 15px;

        .header__note-text {
            display: block;
            margin-left: 10px;
            line-height: 1em;
        }

        .header__note-wrap--mob {
            margin-right: 0;
            flex-shrink: 0;
            flex-basis: 110px;
        }
    }

    &__item {
        padding: 15px 0;
        border-top: 1px solid #dbdbdb;

        &:last-child {
            padding-bottom: 0;
        }
    }

    &__link {
        display: flex;
        align-items: center;
        text-decoration: none;
        color: $dark;
        font-size: 16px;

        > svg,img {
            margin-left: auto;
        }
    }

    &__link-text {
        letter-spacing: 0.03em;
        text-transform: uppercase;
        line-height: 1em;
    }

    .header__circle {
        margin-right: 10px;
    }

    &__address {
        margin-right: 10px;
        width: 18px;
        height: 26px;
    }
}