.contacts {
    width: 100%;
    margin-top: 10px;

    @include response(1023) {
        position: fixed;
        z-index: 24;
        left: 50%;
        transform: translateX(-50%);
        bottom: 29px;
        max-width: 270px;
        margin: 0;
    }

    &__head {
        background: $orange;
        border-radius: 4px;
        height: 60px;
        line-height: 60px;
        display: flex;
        align-items: center;
        padding-left: 20px;
        padding-right: 20px;
        transition: all .3s;

        .anim__arrow-line {
            background: $orange;
        }

        &:hover {
            background: $accent;

            .anim__arrow-line {
                -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
                height: 14px;
            }

            .anim {
                svg {
                    -webkit-transform: translateY(1px) translateX(-50%);
                    transform: translateY(1px) translateX(-50%);
                }
            }
        }

        &.active {
            background: $accent;
            border-bottom-left-radius: 0;
            border-bottom-right-radius: 0;

            @include response(1023) {
                opacity: 0;
                visibility: hidden;
            }

            .anim__arrow-line {
                -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
                height: 14px;
            }

            .anim {
                transform: rotate(180deg);

                svg {
                    transform: translateY(1px) translateX(-50%);
                }
            }
        }

        @include response(1023) {
            height: 50px;
            line-height: 50px;
        }
    }

    &__text {
        font-size: 18px;
        color: white;
        font-weight: 700;
        margin-right: 20px;
        margin-top: 3px;

        @include response(1023) {
            font-size: 16px;
            font-weight: 400;
            margin-right: 10px;
        }
    }

    &__list {
        display: flex;
        align-items: center;

        @include response(1023) {
            margin-left: auto;
        }
    }

    &__social {
        margin-right: 10px;

        &:last-child {
            margin-right: 0;
        }
    }

    &__circle {
        margin-left: auto;
        background: $white;
        border-radius: 50%;
        width: 30px;
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;

        @include response(1023) {
            display: none;
        }

        svg {
            path {
                fill: $orange;
            }
        }

        .anim {
            height: 12px;
        }
    }

    &__dropdown {
        border-radius: 4px;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        border: 1px solid #c2c2c2;
        border-top: 0;
        display: none;
        -webkit-transition: height .3s ease-in-out;
        transition: height .3s ease-in-out;
        overflow: hidden;

        &.show {
            display: block;

            @include response(1023) {
                display: none;
            }
        }
    }

    &__phone-info {
        padding: 18px 20px;
    }

    &__phone-link,
    &__inner-social,
    &__social-list {
        display: flex;
        align-items: center;
    }

    &__inner-social {
        padding: 20px;
        background: #F3F3F3;
    }

    &__phone-link {
        text-decoration: none;
        display: flex;
        align-items: center;

        svg {
            margin-right: 15px;
        }
    }

    &__phone-text {
        font-size: 24px;
        color: $dark;
        line-height: 1em;
    }

    &__social-list {
        margin-right: 15px;
    }
}

.contacts-info {
    display: none;

    @include response(1023) {
        position: fixed;
        left: 0;
        width: 100%;
        min-height: 100vh;
        height: 100%;
        display: block;
        z-index: -1;
        overflow: hidden;
        bottom: 0;
    }

    &.show {
        @include response(1023) {
            z-index: 25;

            .contacts-info__content {
                bottom: 0;
            }

            .contacts-info__overlay {
                opacity: 0.5;
                visibility: visible;
            }
        }
    }

    &__overlay {
        position: fixed;
        bottom: 0;
        left: 0;
        display: block;
        width: 100%;
        background: $dark;
        opacity: 0.5;
        height: 100%;
        visibility: hidden;
        opacity: 0;
        transition: all .4s ease-in-out;
    }

    &__content {
        position: absolute;
        bottom: -100%;
        left: 0;
        width: 100%;
        background: #FFFFFF;
        border-radius: 4px;
        padding-top: 57px;
        padding-bottom: 20px;
        padding-left: 10px;
        padding-right: 10px;
        text-align: center;
        color: $dark;
        transition: all .4s ease-in-out;
    }

    &__close {
        position: absolute;
        top: 9px;
        right: 9px;
        display: block;
        width: 16px;
        height: 16px;

        &::before,
        &::after {
            content: '';
            position: absolute;
            left: 0;
            background: #333333;
            height: 1px;
            width: 100%;
            border-radius: 50px;
        }

        &::before {
            transform: translateY(-50%) rotate(45deg);
        }

        &::after {
            transform: translateY(-50%) rotate(-45deg);
        }
    }

    &__author-info {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__title {
        font-size: 16px;
        margin-bottom: 5px;
    }

    .adt-details__stars {
        margin-bottom: 15px;
    }

    .adt-details__rating-val {
        margin-top: 2px;
    }

    &__date {
        font-size: 14px;
        color: #999;
        margin-bottom: 30px;
    }

    &__phone {
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        text-decoration: none;
        font-size: 24px;
        background: $dark;
        border-radius: 4px;
        height: 50px;
        width: 100%;
        margin-bottom: 20px;

        &-text {
            line-height: 1em;
            margin-top: 5px;
        }
    }

    &__bottom-text {
        font-size: 14px;
        color: #999;
        margin-bottom: 10px;
    }

    &__social {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        &-link {
            width: 50px;
            height: 50px;
            border-radius: 4px;
            margin-right: 5px;
            display: flex;
            justify-content: center;
            align-items: center;
            border: 1px solid #e6e6e6;

            &:last-child {
                margin-right: 0;
            }

            svg {
                width: 30px;
                height: 30px;
            }
        }
    }
}