.interest {
    padding: 100px 0;

    @include response(1023) {
        padding-top: 50px;
        padding-bottom: 50px;
    }

    &__title {
        font-size: 36px;
        margin-bottom: 30px;

        @include response(1023) {
            font-size: 24px;
            margin-bottom: 20px;
        }
    }

    &__row {
        display: flex;
        margin: 0 -20px;

        @include response(1023) {
            flex-direction: column;
            margin: 0;
        }
    }

    &__item {
        width: 50%;
        padding: 0 20px;
        display: flex;

        @include response(1023) {
            width: 100%;
            padding: 0;
            margin-bottom: 20px;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    &__inner {
        padding-top: 30px;
        padding-bottom: 37px;
        padding-left: 20px;
        padding-right: 20px;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        background: #2D676C;
        border-radius: 4px;
        color: white;
        text-decoration: none;
        transition: all .3s ease-in-out;
        &:hover,
        &:focus {
            background: #09454A;

            .interest__btn {
                border-color: $accent;
                background: $accent;
            }

            .button__circle {
                transform: scale(1.2);
                background: white;

                svg {
                    path {
                        fill: $accent;
                    }
                }
            }

            .interest__img {
                transform: scale(1.3);
            }
        }

        @include response(1023) {
            flex-direction: column;
            padding-top: 24px;
            padding-bottom: 20px;
            padding-left: 10px;
            padding-right: 10px;
        }
    }

    &__content {
        margin-right: 20px;
        width: 46.299%;

        @include response(1023) {
            width: 100%;
            margin-right: 0;
        }
    }

    &__subtitle {
        margin-bottom: 17px;
        line-height: 1.3em;
        font-size: 18px;

        &--mob {
            display: none;
        }

        @include response(1023) {
            font-size: 16px;
            margin-bottom: 19px;
            display: none;

            &--mob {
                display: block;
                order: -2;
            }
        }
    }

    &__subtext {
        margin-bottom: 21px;
        font-size: 16px;
        color: rgba(255, 255, 255, 0.8);

        @include response(1023) {
            font-size: 14px;
            margin-bottom: 15px;
        }
    }

    &__phones {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 28px;

        @include response(1023) {
            flex-direction: row;
            margin-bottom: 18px;
        }
    }

    &__phone {
        color: white;
        font-size: 16px;
        margin-bottom: 10px;
        font-weight: 700;
        text-decoration: none;
        line-height: 1em;
        transition: all .3s ease-in-out;
        &:last-child {
            margin-bottom: 0;
        }

        @include response(1023) {
            margin-right: 25px;
            margin-bottom: 0;

            &:last-child {
                margin-right: 0;
            }
        }
    }

    &__btn {
        width: 100%;

        @include response(1023) {
            justify-content: center;

            .button__text {
                margin-right: 10px;
            }
        }
    }

    &__img-wrap {
        position: relative;
        padding-bottom: 116%;
        overflow: hidden;

        @include response(1023) {
            padding-bottom: 45.2%;
        }
    }

    &__sticker {
        position: absolute;
        top: -5px;
        right: -5px;
        display: flex;
        align-items: center;
        z-index: 5;
        padding-top: 6px;
        padding-bottom: 7px;
        padding-left: 8px;
        padding-right: 8px;
        background: $orange;
        border-radius: 4px;

        @include response(1023) {
            height: 18px;
            padding: 3px 5px;
            top: -9px;
            svg {
                max-width: 38px;
                margin-bottom: 1px;
            }
        }
    }

    &__img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        -o-object-fit: cover;
        transition: all .3s ease-in-out;
        transform-origin: 50% 50%;
    }

    &__sticker-text {
        font-size: 14px;
        text-transform: uppercase;
        margin-left: 5px;
        line-height: 0.8em;
        margin-top: 6px;

        @include response(1023) {
            font-size: 10px;
            letter-spacing: 0.03em;
            margin-top: 3px;
        }
    }

    &__thumb-wrap {
        position: relative;
        flex-grow: 1;

        @include response(1023) {
            width: 100%;
            order: -1;
            margin-bottom: 15px;
        }
    }
}