.characteristics {
    display: flex;
    margin-top: 30px;
    margin-bottom: 30px;

    @include response(1023) {
        flex-wrap: wrap;
        margin-top: 15px;
        margin-bottom: 25px;
        padding-right: 10px;
    }

    &__item {
        width: calc(100% / 4);
        border-left: 1px solid $orange;
        padding: 5px 20px;
        min-height: 100px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;

        @include response(1023) {
            width: 50%;
            border-bottom: 2px solid #fff0e6;
            min-height: 39px;
            border-left: 0;
            border-right: 2px solid #fff0e6;
            padding: 5px 20px;
            flex-direction: row;
            justify-content: flex-start;
            align-items: center;

            &:nth-child(2n) {
                border-right: 0;
            }

            &:last-child, &:nth-child(3) {
                border-bottom: 0;
                align-items: flex-end;
            }
        }
    }

    &__title {
        font-size: 36px;
        font-weight: 700;
        margin-bottom: 8px;
        line-height: 0.8em;
        display: flex;
        align-items: center;

        @include response(1023) {
            font-size: 18px;
            margin-bottom: 0;
            margin-right: 5px;
        }

        sup {
            font-size: 25px;
            position: relative;
            top: -11px;

            @include response(1023) {
                font-size: 12px;
                top: -9px;
            }
        }
    }

    &__subtext {
        display: block;
        color: #999999;
        margin-left: 8px;
        font-size: 24px;

        @include response(1023) {
            margin-left: 5px;
            font-size: 14px;
        }
    }

    &__text {
        color: #999;
        font-weight: 400;

        @include response(1023) {
            margin-left: 5px;
            font-size: 14px;
            line-height: 1em;
        }
    }
}