.adt-details {
    background: #E8EDFF;
    border-radius: 4px;

    &__content {
        padding-left: 20px;
        padding-right: 20px;
        padding-top: 30px;
        padding-bottom: 21px;

        .sticker {
            margin-right: 0;
        }

        @include response(1023) {
            padding-left: 10px;
            padding-right: 10px;
            padding-top: 20px;
            padding-bottom: 18px;
        }
    }

    &__top-price {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        margin-bottom: 28px;

        @include response(1023) {
            margin-bottom: 15px;
        }
    }

    &__price {
        display: flex;
        align-items: center;
        margin-right: 20px;
    }

    &__numb {
        font-weight: 900;
        font-size: 36px;
        margin-right: 5px;
        line-height: 0.8em;

        @include response(1023) {
            font-size: 18px;
        }
    }

    &__currency {
        font-size: 14px;
        font-weight: 900;
    }

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-bottom: 28px;
        border-bottom: 1px solid #c4c8d6;
        margin-bottom: 15px;

        @include response(1023) {
            padding-bottom: 15px;
        }
    }

    &__size {
        display: block;
        color: #8e9099;
        margin-right: 20px;

        @include response(1023) {
            font-size: 14px;
            line-height: 1em;
        }
    }

    &__convert-text {
        @include response(1023) {
            font-size: 14px;
            line-height: 1em;
        }
    }

    &__rating-wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 35px;

        @include response(1023) {
            margin-bottom: 20px;
        }
    }

    &__stars,
    &__stars-inner {
        display: flex;
    }

    &__rating-val {
        margin-left: 10px;
        line-height: 1em;

        @include response(1023) {
            font-size: 14px;
        }
    }

    &__star {
        margin-right: 4px;

        &:last-child {
            margin-right: 0;
        }
    }

    &__rating-right {
        display: flex;
        align-items: center;

        .sticker {
            margin-right: 5px;
        }
    }

    &__tooltip {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__author-info {
        display: flex;
        margin-bottom: 30px;
        align-items: center;

        @include response(1023) {
            margin-bottom: 15px;
        }
    }

    &__img-wrap {
        width: 100%;
        max-width: 60px;
        margin-right: 10px;
        border-radius: 4px;

        @include response(1023) {
            display: none;
        }
    }

    &__author-text {
        @include response(1023) {
            display: flex;
            align-items: center;
        }
    }

    &__title {
        font-size: 16px;
        font-weight: 550;
        margin-bottom: 10px;
        line-height: 1em;

        @include response(1023) {
            margin-right: 10px;
            margin-bottom: 0;
        }
    }

    &__info {
        color: #8e9099;

        @include response(1023) {
            font-size: 12px;
            line-height: 1em;
        }
    }

    &__profile-item {
        align-items: center;
        display: flex;
        justify-content: space-between;
        margin-bottom: 15px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__line {
        display: block;
        flex-grow: 1;
        height: 1px;
        background: #d6daeb;
        margin-right: 5px;
        align-self: flex-end;
        margin-bottom: 5px;
    }

    &__profile-title {
        margin-right: 5px;
        color: #829099;
        line-height: 1em;

        @include response(1023) {
            font-size: 14px;
        }
    }

    &__value {
        font-weight: 700;
        line-height: 1em;

        @include response(1023) {
            font-size: 14px;
        }
    }

    &__profile {
        margin-bottom: 32px;
    }

    &__btn {
        display: flex;
        align-items: center;

        .anim__arrow-line {
            height: 7px;
            bottom: initial;
            top: 0;
            transform: translateX(-39%);
            transition: all .4s;
        }

        .anim {
            height: 9px;
            z-index: 5;

            @include response(1023) {
                display: none;
            }
        }

        svg {
            width: 6px;
            height: 4px;
            transform: translateX(-50%) translateY(15%);
        }

        &:hover,
        &:focus {
            .adt-details__btn-text {
                text-decoration: underline;
            }

            .anim__arrow-line {
                height: 10px;
                top: 1px;
            }

            .anim {
                height: 12px;
            }

            svg {
                transform: translateY(2px) translateX(-50%);
            }

            .adt-details__circle {
                &::after {
                    transform: scale(1.1);
                }
            }
        }

        &.active {
            .adt-details__btn-text {
                text-decoration: underline;
                color: $accent;
            }

            .anim__arrow-line {
                height: 10px;
                top: 1px;
            }

            .anim {
                height: 12px;
                transform: rotate(180deg) translateX(-1px);
            }

            svg {
                transform: translateY(2px) translateX(-50%);
            }

            .adt-details__circle {
                width: 22px;
                height: 22px;
                &::after {
                    background: $accent;
                }

                @include response(1023) {
                    &::before {
                        transform: translate(-50%, -50%) rotate(180deg);
                    }
                }
            }
        }
    }

    &__btn-text {
        font-size: 16px;
        margin-right: 10px;
    }

    &__circle {
        position: relative;
        width: 20px;
        height: 20px;
        display: flex;
        justify-content: center;
        align-items: center;

        &::after {
            content: '';
            position: absolute;
            top: 0;
            z-index: 3;
            left: 0;
            width: 100%;
            height: 100%;
            background: $dark;
            transition: all .3s;
            transform-origin: 50% 50%;
            border-radius: 50%;
        }

        @include response(1023) {
            &::before {
                content: '';
                position: absolute;
                top: 50%;
                z-index: 5;
                left: 50%;
                height: 9px;
                width: 6px;
                transform: translate(-50%, -50%);
                background-image: url('../images/arrow-down.svg');
                background-size: cover;
                background-repeat: no-repeat;
                transition: all .4s;
            }
        }
    }
}