.offers {
    padding-top: 50px;
    padding-bottom: 47px;
    background: #E8EDFF;

    @include response(1023) {
        padding-top: 30px;
        padding-bottom: 26px;
    }

    &__title {
        font-size: 36px;
        margin-bottom: 30px;

        @include response(1023) {
            font-size: 24px;
            margin-bottom: 20px;
        }
    }

    &__slider {
        .swiper-wrapper {
            margin: 0 -13px;
            display: flex;
            width: auto;

            @include response(1200) {
                flex-wrap: wrap;
                margin: -13px;
            }

            @include response(1023) {
                margin: 0;
                display: flex;
                width: 100%;
                flex-wrap: nowrap;
            }
        }
    }

    &__img-wrap {
        @include response(1200) {
            img {
                margin: auto;
                width: 100%;
            }
        }
    }

    .swiper-slide {
        padding: 0 13px;
        width: calc(100% / 4);
        display: flex;

        @include response(1200) {
            width: 50%;
            padding: 13px;
        }

        @include response(1023) {
            width: 280px !important;
            padding: 0;
        }
    }

    &__container {
        @include response(1023) {
            padding-right: 0;
        }
    }

    &__inner {
        background: $white;
    }

    &__subtitle {
        margin-bottom: 15px;
        font-size: 18px;
        line-height: 1em;

        @include response(1023) {
            font-size: 16px;
        }
    }

    &__descr {
        font-size: 16px;
        margin-bottom: 15px;

        @include response(1023) {
            margin-bottom: 38px;
        }
    }

    &__content {
        padding-bottom: 15px;
        padding-left: 20px;
        padding-right: 18px;
        padding-top: 29px;

        @include response(1023) {
            padding-top: 20px;
            padding-bottom: 20px;
            padding-left: 10px;
            padding-right: 12px;
        }
    }

    &__bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__price {
        font-size: 16px;
        font-weight: 700;
        margin-left: 10px;

        @include response(1023) {
            padding-right: 17px;
        }
    }

    &__btn {

        &:hover,
        &:active {
            .anim__arrow-line {
                transform: translateY(-50%) scaleX(2);
            }

            svg {
                transform: translateX(10px) translateY(-50%);
            }
        }
    }
}