.sell {
    padding: 50px 0;
    background: url('../images/bg.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;

    @include response(1023) {
        padding-bottom: 46px;
    }

    &__wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include response(1023) {
            flex-direction: column;
            align-items: center;

            .button--large {
                width: 335px;
            }
        }
    }

    &__title {
        font-size: 24px;
        margin-right: 40px;

        @include response(1023) {
            margin-bottom: 30px;
            margin-right: 0;
            white-space: nowrap;
            text-align: center;
            font-weight: 400;
        }
    }
}