.search {
    position: fixed;
    z-index: 23;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100vh;
    height: 100%;
    background: #F3F3F3;
    color: $dark;
    padding: 13px 10px;
    overflow: hidden;
    overflow-y: auto;
    left: -100%;
    transition: all .4s ease-in-out;

    &.show {
        left: 0;
    }

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 17px;
    }

    &__form {
        position: relative;
        width: 100%;
        max-width: 307px;
        margin-right: 5px;
    }

    &__btn {
        position: absolute;
        z-index: 4;
        top: 47%;
        left: 5px;
        transform: translateY(-50%);
        width: 10px;
        height: 10px;
        display: block;

        svg {
            path {
                fill: $dark;
            }
        }
    }

    &__label {
        display: block;
        width: 100%;
    }

    &__input {
        display: block;
        width: 100%;
        height: 30px;
        line-height: 30px;
        padding: 9px 20px;
        color: $dark;
        font-size: 12px;
        font-weight: 400;
        border: 0;
        background: white;
        text-align: left;

        @include placeholder {
            font-size: 12px;
            font-weight: 400;
            color: #c2c2c2;
        }
    }

    &__cancel {
        font-size: 12px;
    }

    &__recently-title {
        margin-bottom: 15px;
        font-size: 12px;
        color: #b9b9b9;
        font-weight: 400;
        line-height: 1em;
    }

    &__recently-wrap {
        background: white;
        padding: 0 10px;
    }

    &__recently-item {
        border-bottom: 1px solid #e6e6e6;
        padding: 9px 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
        &:last-child {
            border-bottom: 0;
        }
    }

    &__recently-result {
        font-size: 12px;
        color: $dark;
        line-height: 1em;
    }

    &__recently-remove {
        position: relative;
        display: block;
        width: 12px;
        height: 12px;

        &::after,
        &::before {
            content: '';
            position: absolute;
            left: 0;
            background: $dark;
            height: 1px;
            width: 100%;
            border-radius: 50px;
        }

        &::after {
            transform: translateY(-50%) rotate(-45deg);
        }

        &::before {
            transform: translateY(-50%) rotate(45deg);
        }
    }
}