.ads {
    display: none;
    -webkit-transition: height .3s ease-in-out;
    transition: height .3s ease-in-out;
    overflow: hidden;

    &.show {
        display: block;
    }

    &__list {
        padding-top: 22px;
    }

    &__item {
        position: relative;
        display: flex;
        align-items: center;
        padding-top: 22px;
        padding-bottom: 33px;
        padding-right: 22px;
        padding-left: 20px;
        transition: all .3s;
        text-decoration: none;
        color: $dark;
        margin-bottom: -1px;

        &::after,
        &::before {
            content: '';
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            background: #babecc;
            height: 1px;
            width: 94.59%;
            transition: all .3s;
        }

        &::after {
            top: 0;
        }

        &::before {
            bottom: 0;
        }

        &:hover,
        &:focus {
            background: #D1DBFF;

            &::after,
            &::before {
                width: 100%;
            }

            .ads__arrow {
                opacity: 1;
                visibility: visible;
            }
        }

        @include response(1023) {
            padding-bottom: 20px;
        }
    }

    &__arrow {
        position: absolute;
        z-index: 3;
        opacity: 0;
        visibility: hidden;
        bottom: 20px;
        right: 20px;
    }

    &__img-wrap {
        margin-right: 10px;

        img {
            border-radius: 4px;
        }

        @include response(1023) {
            display: none;
        }
    }

    &__right-content {
        flex-grow: 1;
    }

    &__top {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        margin-bottom: 9px;

        @include response(1023) {
            margin-bottom: 8px;
        }

        .sticker {
            margin-right: 0;
        }
    }

    &__price-info {
        display: flex;
        align-items: center;
    }

    &__price {
        font-size: 14px;
        display: block;
        line-height: 1em;
        font-weight: 900;
        margin-right: 3px;
    }

    &__currency {
        font-size: 10px;
        display: block;
        font-weight: 400;
        line-height: 1em;
    }

    &__title {
        font-size: 14px;
        margin-bottom: 7px;
        line-height: 1em;
    }

    &__address {
        color: #8e9099;
        font-size: 12px;
        font-style: normal;
    }

    &__bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-left: 10px;
        padding-right: 10px;
        padding-top: 13px;
        padding-bottom: 14px;

        @include response(1023) {
            padding-top: 10px;
            padding-bottom: 11px;
        }
    }

    &__link {
        color: $dark;
        text-decoration: underline;

        @include response(1023) {
            font-size: 12px;
        }

        &:hover,
        &:focus {
            text-decoration: none;
        }
    }
}