.search-nav {
    padding: 15px 0;

    @include response(1023) {
        padding-top: 11px;
        padding-bottom: 20px;
    }

    &__btn {
        display: flex;
        align-items: center;
        text-decoration: none;

        &:hover,
        &:focus {
            .search-nav__btn-icon {
                background: $dark;
            }

            svg {
                path {
                    fill: white;
                }
            }
        }
    }

    &__btn-icon {
        width: 30px;
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 10px;
        border-radius: 4px;
        background: $white;
        transition: all .3s ease-in-out;
    }

    &__text {
        font-size: 12px;
        color: $dark;
    }
}