/* Mixins:
  ========================================================================== */

// px to rem
@function rem($pixels, $context: $mainFontSize) {
    @if (unitless($pixels)) {
      $pixels: $pixels * 1px;
    }
    @if (unitless($context)) {
      $context: $context * 1px;
    }
    @return $pixels / $context * 1rem;
  }
  
  // size to one line
  @mixin size($width, $height: $width) {
    width: $width;
    height: $height;
  }
  
  // placeholder helper
  @mixin placeholder {
    ::-webkit-input-placeholder {
      @content;
    }
    :-moz-placeholder {
      @content;
    }
    ::-moz-placeholder {
      @content;
    }
    :-ms-input-placeholder {
      @content;
    }
  }
  
  
  @mixin font-face(
    $font-family,
    $file-path,
    $weight: normal,
    $style: normal,
    $asset-pipeline: false
  ) {
    @font-face {
      font-family: $font-family;
      font-weight: $weight;
      font-style: $style;
  
      src: url('#{$file-path}.woff') format('woff'),
        url('#{$file-path}.woff2') format('woff2');
      font-display: swap;
    }
  }
  
  @mixin response($screen) {
    @media all and (max-width: $screen+"px") {
        @content;
    }
  }
  
  @mixin mr($screen) {
    @media all and (min-width: $screen+"px") {
        @content;
    }
  }
  
  @function vw($target, $from: 1920) {
    $vw-context: ($from * 0.01) * 1px;
    $vw-value: ($target/$vw-context);
    @return $vw-value * 1vw;
  }
  
  @mixin adaptFont($responsive, $min, $max: false, $fallback: false) {
    $responsive-unitless: $responsive / ($responsive - $responsive + 1);
    $dimension: if(unit($responsive) == "vh", "height", "width");
    $min-breakpoint: $min / $responsive-unitless * 100;
  
    @media (max-#{$dimension}: #{$min-breakpoint}) {
        font-size: $min;
    }
  
    @if $max {
        $max-breakpoint: $max / $responsive-unitless * 100;
  
        @media (min-#{$dimension}: #{$max-breakpoint}) {
            font-size: $max;
        }
    }
  
    @if $fallback {
        font-size: $fallback;
    }
  
    font-size: $responsive;
  }
  
  @mixin adapt($name, $responsive, $min, $max: false) {
    $responsive-unitless: $responsive / ($responsive - $responsive + 1);
    $dimension: if(unit($responsive) == "vh", "height", "width");
    $min-breakpoint: $min / $responsive-unitless * 100;
  
    @media (max-#{$dimension}: #{$min-breakpoint}) {
        #{$name}: $min;
    }
  
    @if $max {
        $max-breakpoint: $max / $responsive-unitless * 100;
  
        @media (min-#{$dimension}: #{$max-breakpoint}) {
            #{$name}: $max;
        }
    }
  
    #{$name}: $responsive;
  }
  