.adt {
    padding-top: 15px;
    padding-bottom: 100px;

    @include response(1023) {
        padding-top: 0;
        padding-bottom: 50px;
    }

    &__top {
        display: flex;
        align-items: center;
        margin-bottom: 25px;

        @include response(1023) {
            margin-bottom: 30px;
        }
    }

    &__title {
        font-size: 24px;
        font-weight: 400;

        .sticker {
            display: inline-block;
            vertical-align: middle;
            margin-bottom: 3px;
            margin-right: 10px;

            @include response(1023) {
                margin-right: 3px;
                vertical-align: initial;
                margin-bottom: 6px;
            }
        }

        @include response(1023) {
            font-size: 18px;
        }
    }

    &__address-top {
        display: flex;
        align-items: center;
        margin-bottom: 30px;

        @include response(1023) {
            margin-bottom: 15px;
        }
    }

    &__address-text {
        margin-right: 30px;

        @include response(1023) {
            margin-right: 0;
            font-weight: 550;
        }
    }

    &__view-map {
        display: flex;
        align-items: center;
        color: #999;

        &:hover,
        &:focus {
            color: $dark;
        }

        &--mob {
            display: none;
        }

        @include response(1023) {
            display: none;

            &--mob {
                display: inline-block;
                color: $orange;
                font-size: 14px;
                font-weight: 400;
            }
        }
    }

    &__map-icon {
        margin-right: 5px;
        max-width: 10px;
        width: 100%;
    }

    &__map-text {
        color: currentColor;
        text-decoration: underline;
    }

    &__metro-info {
        width: 100%;
        display: flex;
        align-items: center;
        margin-bottom: 35px;

        @include response(1023) {
            flex-direction: column;
            margin-bottom: 30px;
            align-items: flex-start;
        }
    }

    &__metro-title-text {
        @include response(1023) {
            font-size: 14px;
        }
    }

    &__metro-item {
        margin-right: 30px;
        display: flex;
        align-items: center;

        @include response(1023) {
            margin-right: 0;
            margin-bottom: 10px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    &__metro-title,
    &__metro-time {
        display: flex;
        align-items: center;
    }

    &__metro-title,
    &__metro-icon {
        margin-right: 5px;
    }

    &__metro-time-icon {
        margin-right: 5.48px;

        @include response(1023) {
            margin-right: 3px;
        }
    }

    &__metro-time-text {
        color: #999999;
        line-height: 1em;

        @include response(1023) {
            font-size: 14px;
        }
    }

    &__info {
        display: flex;
        align-items: center;
        margin-bottom: 10px;
    }

    &__viewed {
        display: flex;
        align-items: center;
    }

    &__date {
        display: block;
        color: #808080;
        margin-right: 20px;
        font-size: 14px;

        @include response(1023) {
            font-size: 12px;
            margin-right: 15px;
        }
    }

    &__view-icon {
        margin-right: 6px;

        @include response(1023) {
            margin-right: 3px;
        }
    }

    &__view-count {
        color: #808080;
        font-size: 14px;

        @include response(1023) {
            font-size: 12px;
        }
    }

    &__row {
        display: flex;

        @include response(1023) {
            flex-wrap: wrap;
        }
    }

    &__item {
        flex-grow: 1;

        &--left {
            width: 63.547%;
            margin-right: 30px;

            @include response(1023) {
                width: 100%;
            }
        }

        &--right {
            @include response(1023) {
                padding-right: 10px;
            }
        }

        @include response(1023) {
            width: 100%;
        }
    }

    &__btn-full {
        position: absolute;
        top: 20px;
        left: 20px;
        width: 30px;
        height: 30px;
        z-index: 10;

        @include response(1023) {
            width: 20px;
            height: 20px;
            top: 10px;
            left: 10px;
        }
    }

    &__btn-icon {
        &--mob {
            display: none;
        }

        @include response(1023) {
            display: none;

            &--mob {
                display: block;
            }
        }
    }

    &__gallery-count {
        position: absolute;
        right: 20px;
        bottom: 13px;
        z-index: 10;
        display: flex;
        align-items: flex-start;

        @include response(1023) {
            bottom: 11px;
            right: 45px;
        }
    }

    &__gallery-count-current {
        display: block;
        margin-right: 5px;
        font-size: 36px;
        color: white;
        line-height: 1em;
        margin-bottom: 2px;

        @include response(1023) {
            font-size: 18px;
            margin-right: 2px;
        }

    }

    &__gallery-count-total {
        color: rgba(255, 255, 255, 0.5);
        font-size: 24px;

        &::before {
            content: '/';
            color: currentColor;
            font-size: 24px;

            @include response(1023) {
                font-size: 14px;
            }
        }

        @include response(1023) {
            font-size: 14px;
        }
    }

    &__gallery {
        .swiper-container {
            background: #D8D8D8;
            border-radius: 4px;

            @include response(1023) {
                background: 0 0;
                border-radius: 0;
            }
        }

        .swiper-slide {
            height: auto;
            display: flex;
            align-items: center;
            justify-content: center;

            @include response(1023) {
                background: #D8D8D8;
                width: 330px !important;
                border-radius: 4px;
            }
        }

        @include response(1023) {

            .swiper-button-prev,
            .swiper-button-next {
                display: none;
            }
        }
    }

    &__descr {
        margin-bottom: 15px;

        @include response(1023) {
            padding-right: 10px;
            margin-bottom: 12px;
        }
    }

    &__descr-content {
        border-radius: 4px;
        background: $white;
        padding: 30px 20px 33px;

        @include response(1023) {
            padding: 20px 10px;

            p {
                font-size: 16px;
            }
        }
    }

    &__descr-title {
        margin-bottom: 20px;
        line-height: 1em;

        @include response(1023) {
            font-size: 16px;
            margin-bottom: 15px;
        }
    }

    &__container-gallery {
        @include response(1023) {
            padding-right: 0;
        }
    }

    &__footer {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 15px;

        @include response(1023) {
            margin-top: 10px;
            margin-bottom: 50px;
            padding-right: 10px;
        }
    }

    &__footer-left,
    &__footer-right {
        display: flex;
        align-items: center;

        @include response(1023) {
            flex-direction: column;
            align-items: flex-start;
        }
    }

    &__footer-right {
        @include response(1023) {
            text-align: right;
            flex-direction: column-reverse;
            align-items: flex-end;
        }
    }

    &__footer-link {
        text-decoration: underline;
        display: flex;
        align-items: center;
        margin-right: 30px;
        font-size: 14px;
        color: #999;

        &:last-child {
            margin-right: 0;
        }

        svg {
            margin-left: 5px;
            fill: currentColor;

            path {
                fill: currentColor;
            }
        }

        &:hover,
        &:focus {
            color: $blue;
        }

        @include response(1023) {
            margin-right: 0;
            margin-bottom: 10px;
            line-height: 1em;
            font-size: 12px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    &__footer-info {
        font-size: 14px;
        margin-right: 20px;
        color: #999;
        display: flex;
        align-items: center;

        &:last-child {
            margin-right: 0;
        }

        @include response(1023) {
            margin-right: 0;
            margin-bottom: 10px;
            line-height: 1em;
            font-size: 12px;

            &:first-child {
                margin-bottom: 0;
            }
        }
    }

    &__right-content {
        position: sticky;
        top: 20px;

        @include response(1023) {
            position: static;
        }
    }

    &__right-top {
        padding-left: 20px;
        padding-right: 20px;
        margin-bottom: 10px;

        @include response(1023) {
            padding: 0;
        }
    }

    &__add-btn {
        display: flex;
        align-items: center;
        text-decoration: none;

        &:hover,
        &:focus {
            .adt__add-text {
                text-decoration: underline;
                color: $dark
            }

            .adt__add-circle {
                background: $accent;

                svg {
                    width: 10px;
                    height: 10px;
                }
            }
        }
    }

    &__add-text {
        color: #999;
        font-size: 14px;
        transition: all .3s;
        line-height: 0.8em;
    }

    &__add-circle {
        margin-right: 10px;
        width: 20px;
        height: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: $orange;
        transition: all .3s;
        border-radius: 50%;

        svg {
            width: 6px;
            height: 6px;
            transition: all .3s;

            path {
                fill: white;
            }
        }

        @include response(1023) {
            margin-right: 5px;
        }
    }
}