.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100vh;
    height: 100%;
    overflow: hidden;
    overflow-y: auto;
    padding: 15px;
    z-index: -1;
    opacity: 0;
    visibility: hidden;
    transition: all .4s;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

    &.show {
        opacity: 1;
        visibility: visible;
        z-index: 30;
    }

    &__overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 5;
        height: 100%;
        background: $dark;
        opacity: 0.5;
    }

    &__content-wrap {
        position: relative;
        z-index: 6;
        width: 100%;
        max-width: 1200px;
    }

    &__slider {
        max-height: 78vh;


        .swiper-slide {
            height: auto;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }

    &__close {
        position: absolute;
        z-index: 5;
        display: block;
        width: 40px;
        height: 40px;
        top: -50px;
        right: 0;
        background: #F4F4F4;
        border-radius: 50%;

        &::after,
        &::before {
            content: '';
            position: absolute;
            width: 30px;
            height: 2px;
            left: 50%;
            display: block;
            background: #2D2D2D;
            transition: all .3s;
        }

        &::after {
            transform: translateX(-50%) rotate(-45deg);
        }

        &::before {
            transform: translateX(-50%) rotate(45deg);
        }

        &:hover,
        &:focus,
        &:active {

            &::after,
            &::before {
                background: $orange;
            }
        }
    }

    &__slider {

        .swiper-button-prev,
        .swiper-button-next {
            width: 80px;
            height: 80px;
            background: $dark;

            @include response(1023) {
                width: 40px;
                height: 40px;
            }

            &::before {
                width: 33px;
                height: 18px;

                @include response(1023) {
                    width: 20px;
                    height: 10px;
                    background-size: cover;
                }
            }
        }

        .swiper-button-prev {
            &::before {
                background-image: url('../images/large-prev.svg');
            }
        }

        .swiper-button-next {
            &::before {
                background-image: url('../images/large-next.svg');
            }
        }
    }
}