.additional-info {
    border-radius: 4px;
    background: $white;

    @include response(1023) {
        margin-right: 10px;
    }

    &__head {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-top: 32px;
        padding-bottom: 35px;
        padding-left: 20px;
        padding-right: 20px;
        cursor: pointer;

        .anim {
            height: 12px;
        }

        &.active {
            .additional-info__btn {
                background: $accent;
            }

            .anim {
                transform: rotate(180deg);
            }

            .anim__arrow-line {
                transform: translateX(-50%);
                height: 14px;
            }

            svg {
                transform: translateY(1px) translateX(-50%);
            }

        }

        &:hover {

            .anim__arrow-line {
                transform: translateX(-50%);
                height: 14px;
            }

            svg {
                transform: translateY(1px) translateX(-50%);
            }
        }

        @include response(1023) {
            padding-top: 20px;
            padding-bottom: 19px;
            padding-left: 10px;
            padding-right: 10px;
        }
    }

    &__title {
        margin-right: 20px;
    }

    &__btn {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 30px;
        height: 30px;
        background: $dark;
        border-radius: 50%;
        transition: all .3s ease-in-out;
    }

    &__content {
        transition: height .3s ease-in-out;
        overflow: hidden;
        display: none;

        &.show {
            display: block;
        }
    }

    &__row {
        padding-top: 10px;
        padding-bottom: 29px;
        display: flex;
        align-items: flex-start;
        margin: 0 -36.5px;
        padding-left: 20px;
        padding-right: 56px;

        @include response(1023) {
            margin: 0;
            flex-direction: column;
            padding: 33px 10px;
            padding-top: 27px;
        }
    }

    &__col {
        width: 50%;
        padding: 0 36.5px;

        @include response(1023) {
            padding: 0;
            width: 100%;
            margin-bottom: 22px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    &__item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 15px;

        &:last-child {
            margin-bottom: 0;
        }

        @include response(1023) {
            margin-bottom: 22px;
        }
    }

    &__active-icon {
        opacity: 0;
    }

    &__subtitle {
        font-size: 14px;
        margin-right: 7px;
        font-weight: 400;
        color: #7d7d7d;
        line-height: 0.9em;
    }

    &__line {
        display: block;
        flex-grow: 1;
        margin-right: 7px;
        height: 1px;
        background: #c7c7c7;
        align-self: flex-end;
        margin-bottom: 3px;
    }

    &__text {
        font-size: 14px;
        color: #000;
        line-height: 0.9em;
    }
}