.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 20;
    padding-top: 15px;
    padding-bottom: 20px;
    background: $white;
    transition: all .4s;

    @include response(1023) {
        padding-top: 14px;
        padding-bottom: 15px;
        background: #F3F3F3;
    }

    &.out {
        opacity: 0;
        visibility: hidden;

        @include response(1023) {
            opacity: 1;
            visibility: visible;
        }
    }

    &__logo {
        display: block;
        width: 100%;
        max-width: 150px;
        margin-right: 30px;

        @include response(1023) {
            max-width: 100px;
            flex-shrink: 0;
        }
    }

    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 39px;

        @include response(1023) {
            margin-bottom: 0;
        }
    }

    &__search {
        width: 100%;
        max-width: 270px;
        margin-right: 30px;
    }

    &__search-form {
        position: relative;

        @include response(1023) {
            display: none;
        }
    }

    &__search-btn {
        position: absolute;
        left: 0;
        bottom: 5px;
        width: 16px;
        height: 16px;
    }

    &__search-label {
        display: block;
    }

    &__search-input {
        border: 0;
        width: 100%;
        display: block;
        border-bottom: 1px solid #CEDBDD;
        padding-bottom: 2px;
        padding-left: 26px;
        padding-right: 10px;
        background: 0 0;
        color: $dark;
        font-size: 12px;
        height: 21px;
        font-weight: 400;
        transition: border-color .4s ease-in-out;
        caret-color: $orange;

        @include placeholder {
            color: #b9b9b9;
            font-weight: 400;
            font-size: 12px;
        }

        &:focus {
            outline: none;
            border-color: $orange;
        }
    }

    &__common {
        display: flex;
        align-items: center;
    }

    &__note-wrap {
        display: flex;
        align-items: center;
        flex-basis: 104px;
        text-decoration: none;
        margin-right: 33px;

        &--mob {
            display: none;
        }

        @include response(1023) {
            margin-right: 20px;
            display: none;

            &--mob {
                display: flex;
            }
        }
    }

    &__note-circle {
        background: $orange;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        flex-basis: 20px;
        padding: 4px 6px;
        flex-shrink: 0;
        margin-right: 10px;

        @include response(1023) {
            margin-right: 0;
            flex-basis: 26px;
            padding-bottom: 7px;
            padding-top: 7px;
        }
    }

    &__note-count {
        font-weight: 700;
        font-size: 14px;
        color: white;
        display: block;
        margin-top: 3px;
        line-height: 0.7em;
    }

    &__note-text {
        color: #939393;
        font-size: 14px;

        @include response(1023) {
            display: none;
        }
    }

    &__button {
        margin-right: 34px;

        @include response(1023) {
            display: none;
        }
    }

    &__langs {
        display: flex;
        align-items: center;

        @include response(1023) {
            display: none;

            &--mob {
                display: flex;

                .header__lang-item {
                    padding-right: 10px;
                    margin-right: 10px;
                }
            }
        }
    }

    &__lang-item {
        position: relative;
        padding-right: 5px;
        margin-right: 5px;

        &::after {
            content: '';
            right: 0;
            position: absolute;
            top: 50%;
            height: 13px;
            width: 1px;
            background: $dark;
            transform: translateY(-50%);
        }

        &:last-child {
            margin-right: 0;
            padding-right: 0;

            &::after {
                display: none;
            }
        }

        &.active {
            .header__lang-text {
                color: #939393;
                text-decoration: none;
            }
        }

        a {

            &:hover,
            &:focus {
                color: $accent;
            }
        }
    }

    &__lang-text {
        color: $dark;
        text-decoration: underline;
        transition: color .3s ease-in-out;
    }

    &__bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__location {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;

        @include response(1023) {
            display: none;
        }

        &:hover,
        &:focus {
            .header__location-text {
                color: $orange;
            }
        }

        &.active {
            .header__location-text {
                color: $orange;
            }

            .header__location-select {
                opacity: 1;
                visibility: visible;
                z-index: 10;
            }

            .header__nav-icon {
                transform: rotate(180deg);
            }
        }

        .header__nav-icon {
            transition: all .4s;
        }
    }

    &__location-icon {
        width: 100%;
        max-width: 10px;
        margin-right: 5px;
    }

    &__location-text {
        display: flex;
        align-items: center;
        font-size: 14px;
        text-decoration: underline;
        color: $dark;
        line-height: 1em;
        transition: color .3s ease-in-out;

        @include response(1200) {
            font-size: 12px;
        }

        @include response(1023) {
            font-size: 14px;
        }
    }

    &__nav {
        margin-left: auto;

        @include response(1023) {
            display: none;
        }
    }

    &__nav-list {
        display: flex;
        align-items: center;
    }

    &__nav-item {
        margin-right: 30px;

        &:last-child {
            margin-right: 0;
        }

        @include response(1200) {
            margin-right: 15px;
        }
    }

    &__nav-link {
        text-transform: uppercase;
        display: flex;
        align-items: center;
        font-size: 16px;
        letter-spacing: 0.03em;
        font-weight: 400;
        text-decoration: none;
        color: $dark;
        line-height: 1em;
        transition: color .3s ease-in-out;

        &:hover,
        &:focus {
            color: $orange;
            text-decoration: underline;
        }

        @include response(1200) {
            font-size: 14px;
        }
    }

    &__nav-icon {
        margin-left: 5px;
        margin-bottom: 3px;
    }

    &__location-select {
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        z-index: -1;
        visibility: hidden;
        padding-top: 42px;
        min-width: 208px;
        transition: all .3s ease-in-out;
    }

    &__burger {
        position: relative;
        display: none;
        width: 26px;
        height: 24px;
        flex-shrink: 0;

        &-line {
            position: absolute;
            background: $dark;
            height: 3px;
            left: 0;
            width: 100%;
            display: block;
            border-radius: 50px;
            transition: all .3s;

            &--one {
                top: 0;
            }

            &--two {
                top: 50%;
                transform: translateY(-50%);
            }

            &--three {
                bottom: 0;
            }
        }

        &.active {
            .header__burger-line {
                &--one {
                    top: 50%;
                    transform: translateY(-50%) rotate(-45deg);
                }

                &--two {
                    opacity: 0;
                    visibility: hidden;
                }

                &--three {
                    top: 50%;
                    bottom: auto;
                    transform: translateY(-50%) rotate(45deg);
                }
            }
        }

        @include response(1023) {
            display: block;
        }
    }

    &__circle {
        display: none;

        @include response(1023) {
            display: block;
            margin-right: 15px;
            border-radius: 50%;
            width: 26px;
            height: 26px;
            display: flex;
            justify-content: center;
            align-items: center;
            background: $orange;
            flex-shrink: 0;

            svg {
                path {
                    fill: white;
                }
            }

            &--plus {
                svg {
                    width: 10px;
                    height: 10px;
                }
            }
        }
    }

    .js-hide-el {
        position: relative;
        transition: all .3s;

        &.hide {
            opacity: 0;
            visibility: hidden;
            z-index: -1;
        }
    }
}