.breadcrumbs {
    background: #FBFBFB;
    padding-top: 5px;
    padding-bottom: 4px;

    @include response(1023) {
        overflow: auto;
        -webkit-overflow-scrolling: touch;
        padding-top: 9px;
        padding-bottom: 7px;
        scrollbar-color: 0 0 0 0;

        &::-webkit-scrollbar-thumb {
            background: transparent;
        }

        &::-webkit-scrollbar {
            background: transparent;
        }
    }

    &__list {
        display: flex;
        align-items: center;
        flex-wrap: wrap;

        @include response(1023) {
            flex-wrap: nowrap;
        }
    }

    &__item {
        margin-right: 6px;
        display: flex;
        align-items: center;

        &::after {
            content: '/';
            font-size: 14px;
            color: #979797;
            margin-left: 5px;

            @include response(1023) {
                line-height: 1em;
            }

        }

        &:last-child {
            margin-right: 0;
            padding-right: 0;

            &::after {
                display: none;
            }

            @include response(1023) {
                padding-right: 10px;
            }
        }

        @include response(1023) {
            white-space: nowrap;
            margin-right: 10px;
        }
    }

    &__text {
        display: block;
        font-size: 14px;
        color: #979797;
        text-decoration: underline;

        @include response(1023) {
            line-height: 1em;
        }
    }

    &__link {
        text-decoration: none;

        &:hover,
        &:focus {
            text-decoration: underline;
        }
    }
}